
import { onMounted, ref, watch } from "vue";
import ApiService from "../../core/services/ApiService";
import { useI18n } from "vue-i18n";
import { useField } from "vee-validate";

export default {
  name: "LocationsSelect",
  props: {
    location_id: {
      type: String,
      default: "",
    },
    showLabel: {
      type: Boolean,
      default: false,
    },
    size: {
      type: String,
      default: "small",
    },
    required: {
      type: Boolean,
      default: true,
    },
  },
  emits: ["update:location_id"],

  setup(props) {
    const { t } = useI18n();
    const inputLocation = ref({
      loading: false,
      options: [],
      list: [],
    });

    const isLocationRequired = (value) => {
      if (props.required && !value) {
        return t("rlocation");
      }
      return true;
    };

    const { errorMessage, value: element_id } = useField(
      "location",
      isLocationRequired
    );

    const getLocation = () => {
      ApiService.get("/api/location").then(({ data }) => {
        inputLocation.value.list = data;
        inputLocation.value.options = data;
      });
    };
    const selectLocation = (query) => {
      if (query !== "") {
        inputLocation.value.loading = true;
        setTimeout(() => {
          inputLocation.value.loading = false;
          inputLocation.value.options = inputLocation.value.list.filter(
            (item: Record<string, any>) => {
              return item.name.toLowerCase().indexOf(query.toLowerCase()) > -1;
            }
          );
        }, 200);
      } else {
        inputLocation.value.options = [];
      }
    };

    watch(
      () => props.location_id,
      (first) => {
        if (first) {
          element_id.value = first;
        }
      }
    );

    onMounted(() => getLocation());

    return {
      element_id,
      inputLocation,
      errorMessage,
      getLocation,
      selectLocation,
    };
  },
};
