
import { defineComponent, onMounted, ref } from "vue";
import { Form, Field, ErrorMessage } from "vee-validate";
import ApiService from "@/core/services/ApiService";
import { useI18n } from "vue-i18n";
import LocationsSelect from "@/components/catalogs-select/LocationsSelect.vue";

interface FormCatalog {
  id?: number;
  name: string;
  code: string;
  location_id: string;
  port_authority: string;
  customs_import: string;
  customs_export: string;
  address_route_edi: string;
  color: string;
}

export default defineComponent({
  name: "PortsForm",
  components: {
    LocationsSelect,
    Form,
    Field,
    ErrorMessage,
  },
  props: {
    entityId: String,
  },

  setup(props, { emit }) {
    const { t } = useI18n();
    const submitButton = ref<HTMLElement | null>(null);
    const form = ref<FormCatalog>({
      name: "",
      code: "",
      location_id: "",
      port_authority: "",
      customs_import: "",
      customs_export: "",
      address_route_edi: "",
      color: "",
    });

    const isNameRequired = (value) => {
      if (!value) {
        return t("rname");
      }
      return true;
    };

    onMounted(() => {
      if (props.entityId) {
        ApiService.get("/api/ports/" + props.entityId).then(({ data }) => {
          form.value = { ...data };
        });
      }
    });

    const createElement = async (resetForm) => {
      try {
        const resp = (await ApiService.post("/api/ports", form.value)).data;
        emit("success", resp);
        resetForm();
        submitButton.value?.removeAttribute("data-kt-indicator");
      } catch (error) {
        submitButton.value?.removeAttribute("data-kt-indicator");
      }
    };

    const updateElement = (resetForm) => {
      ApiService.put("/api/ports/" + props.entityId, form.value)
        .then(({ data }) => {
          emit("success", data);
          submitButton.value?.removeAttribute("data-kt-indicator");
          form.value = data;
          resetForm();
        })
        .catch(() => {
          submitButton.value?.removeAttribute("data-kt-indicator");
        });
    };

    const onSumbit = (value, { resetForm }) => {
      if (submitButton.value) {
        submitButton.value.setAttribute("data-kt-indicator", "on");
        if (form.value.id) {
          updateElement(resetForm);
        } else {
          createElement(resetForm);
        }
      }
    };

    return {
      submitButton,
      form,
      onSumbit,
      isNameRequired,
      updateElement,
      createElement,
    };
  },
});
